import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import BlockContent from "@sanity/block-content-to-react"

const StyledContainer = styled.div`
  color: rgb(17, 17, 17);
  background-color: rgb(255, 255, 248, 0.4);
  padding: 1rem;
  text-align: justify;
  font-size: 1.4rem;

  figure:nth-of-type(2n + 1) {
    float: left;
    margin-right: 1rem;
    margin-bottom: 1rem;
    display: inline-flex;
  }

  figure:nth-of-type(2n) {
    float: right;
    margin-left: 1rem;
    margin-bottom: 0;
    display: inline-flex;
  }

  figure:nth-last-of-type(2) {
    padding: 0px 2px 0px 60px;
  }

  figure:nth-last-of-type(1) {
    padding: 0 1rem 0 1.5rem;
  }

  img {
    margin-bottom: 0;
  }
`

export default function Section({ data }) {
  return (
    <StyledContainer>
      <BlockContent blocks={data.section._rawContent} />
      <br />
      <br />
      <hr />
    </StyledContainer>
  )
}

export const query = graphql`
  query($slug: String!) {
    section: sanitySection(slug: { current: { eq: $slug } }) {
      name
      id
      _rawContent(resolveReferences: { maxDepth: 10 })
    }
  }
`
